import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AdminComponent } from './admin.component';
import { AuthGuard } from '../../shared/guard';
//import { Auth2Guard } from '../../shared/guard';
const routes: Routes = [
    {
        path: '', component: AdminComponent,
        children: [
            { path: '', redirectTo: 'dashboard' },
            { path: 'dashboard', loadChildren: './dashboard/dashboard.module#DashboardModule',canActivate: [AuthGuard]},
            { path: 'itemGroupMaster', loadChildren: './inventory/item-group-master/item-group-master.module#ItemGroupMasterModule',canActivate: [AuthGuard],canActivateChild:[AuthGuard]  },
            { path: 'itemMaster', loadChildren: './inventory/item-master/item-master.module#ItemMasterModule',canActivate: [AuthGuard] ,canActivateChild:[AuthGuard]  },
            { path: 'purchase', loadChildren: './inventory/purchase/purchase.module#PurchaseModule',canActivate: [AuthGuard]  },
            { path: 'recipes', loadChildren: './inventory/recipes/recipes.module#RecipesModule',canActivate: [AuthGuard],canActivateChild:[AuthGuard]   },
            { path: 'table', loadChildren: './KOT/table/table.module#TableModule',canActivate: [AuthGuard] ,canActivateChild:[AuthGuard]  },
            { path: 'tablemaster', loadChildren: './KOT/table-master/table-master.module#TableMasterModule',canActivate: [AuthGuard] ,canActivateChild:[AuthGuard]  },
            { path: 'menugroup', loadChildren: './KOT/menu-group-master/menu-group-master.module#MenuGroupMasterModule',canActivate: [AuthGuard],canActivateChild:[AuthGuard]   },
            { path: 'menumaster', loadChildren: './KOT/menu-master/menu-master.module#MenuMasterModule',canActivate: [AuthGuard],canActivateChild:[AuthGuard]   },
            { path: 'order', loadChildren: './KOT/order/order.module#OrderModule',canActivate: [AuthGuard]  },
            { path: 'liveorder', loadChildren: './KOT/live-order/live-order.module#LiveOrderModule',canActivate: [AuthGuard]  },
            { path: 'billing/:orderId', loadChildren: './KOT/billing/billing.module#BillingModule',canActivate: [AuthGuard]  },
            { path: 'report', loadChildren: './report/report.module#ReportModule',canActivate: [AuthGuard]  },
            { path: 'employee', loadChildren: './HRM/employee/employee.module#EmployeeModule',canActivate: [AuthGuard] ,canActivateChild:[AuthGuard]  },
            { path: 'customer', loadChildren: './CRM/customer/customer.module#CustomerModule',canActivate: [AuthGuard]  },
            { path: 'vendor', loadChildren: './CRM/vendor/vendor.module#VendorModule',canActivate: [AuthGuard] ,canActivateChild:[AuthGuard]  },
            { path: 'gst', loadChildren: './settings/gst/gst.module#GstModule',canActivate: [AuthGuard],canActivateChild:[AuthGuard]   },
            { path: 'accesscontrol', loadChildren: './settings/accesscontrol/accesscontrol.module#AccesscontrolModule',canActivate: [AuthGuard] ,canActivateChild:[AuthGuard]  },
            { path: 'stock', loadChildren: './stock/stock.module#StockModule' },
        ]}
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class AdminRoutingModule {
}
